import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllocationComponent } from './allocation.component';
import { OrderLogDetailsComponent } from './components/order-log-details/order-log-details.component';
import { ViewDepositedChequesComponent } from './components/view-deposited-cheques/view-deposited-cheques.component';

const routes: Routes = [
  { path: '', component: AllocationComponent },
  { path: 'order-log-detail/:orderId', component: OrderLogDetailsComponent },
  { path: 'view-deposited-cheques/:orderId', component: ViewDepositedChequesComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AllocationRoutingModule { }
