import { Component, OnInit } from '@angular/core';
import { TreasuryService } from '../petty-cash-treasury/services/treasury.service';
import { HeaderService } from 'src/app/services/header.service';

@Component({
  selector: 'app-allocation',
  templateUrl: './allocation.component.html',
  styleUrls: ['./allocation.component.scss'],
})
export class AllocationComponent {
  constructor(
    public headerService: HeaderService,
    public treasuryService: TreasuryService
  ) {
    this.headerService.setTitle('Allocation')
  }

  iscreateAllocation: boolean;
  selected: number = 1;
  goToPage(value) {
    this.selected = value;
    this.iscreateAllocation = false;
    if(value == 1)
      this.headerService.setTitle('Allocation')
    else
    this.headerService.setTitle('Allocation > Allocation Orders Log')
  }

  createAlocation() {
    this.iscreateAllocation = true;
    this.headerService.setTitle('Allocation > Create Allocation Order')
    this.selected = 0;
  }
}
