
import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { RegisterSettlementService } from '../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { CommonService } from '../../../../../../services/common.service';
import { TreasuryService } from '../../../petty-cash-treasury/services/treasury.service';
import { HeaderService } from '../../../../../../services/header.service';
@Component({
  selector: 'orders-log',
  templateUrl: './orders-log.component.html',
  styleUrls: ['./orders-log.component.scss']
})
export class OrdersLogComponent {
  searchText: string = '';
  filterParams: string = undefined;
  sort: number = 1;
  totalRecords: number = 0;
  pageNo: number = 0;
  pageSize: number = 6;
  pagin: number;
  pages: any[] = [];
  allocationOrderList: any[] = [];
  statusList: any[] = [];
  usersList: any[] = [];
  orderLogFilterForm: FormGroup;
  @ViewChild('success') success: ElementRef;
  constructor(private _registerSettlementService: RegisterSettlementService,
    private _headerService: HeaderService, 
    private treasuryService: TreasuryService, 
    private fb: FormBuilder, public datepipe: DatePipe) {
  }
  initFilterForm() {
    this.orderLogFilterForm = this.fb.group({
      search: [''],
      createdBy: [null],
      date: [''],
      status: [null],
    })
  }
  ngOnInit() {
    //this._headerService.setTitle('Allocation Orderes Log');
    this.getAllocationOrders(this.filterParams);
    this.getStatusList(14);
    this.getUsersList();
    this.initFilterForm();
    this.orderLogFilterForm.valueChanges.subscribe(value => {
      let dateObj = { fromDate: '', toDate: '' };
      let formValues;
      if (value['date']) {
        dateObj['fromDate'] = this.datepipe.transform(value['date'][0], 'yyyy-MM-dd');
        dateObj['toDate'] = this.datepipe.transform(value['date'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...dateObj };
      delete formValues['date'];
      let isEmpty = true;
      for (let obj in value) {
        if (value[obj]) {
          this.pageNo = 0;
          isEmpty = false;
        }
        if (formValues[obj] == null) {
          formValues[obj] = ''
        }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      if (isEmpty) {
        this.filterParams = undefined;
        this.pageNo = 0;
      }
      console.log(this.filterParams);
      this.getAllocationOrders(this.filterParams)
    })
  }
  navigateTo(value, sessionId) {
  }

  resetDate() {
    this.orderLogFilterForm.controls['date'].setValue('');
  }
  resetOrderNo() {
    this.orderLogFilterForm.controls['search'].setValue('');
  }
  getAllocationOrders(filterParams = this.filterParams) {
    let defaultParams = `pageNo=${this.pageNo}&sort=${this.sort}&pageSize=${this.pageSize}`
    this.treasuryService.getAllocationOrders((filterParams && filterParams + `&${defaultParams}`) || (`?${defaultParams}`)).subscribe(response => {
      this.allocationOrderList = response?.data;
      this.totalRecords = response?.totalRecordCount;
      this.pagin = Math.ceil(this.totalRecords / 6);
      this.pages = _.range(this.pagin);
    })
  }
  getStatusList(id) {
    this._registerSettlementService.getStatusList(id).subscribe(response => {
      this.statusList = response.data;
    })
  }
  getUsersList() {
    let params = `?Status=2001&Invitation=2004&PageNo=0&PageSize=1000`
    this.treasuryService.getUsers(params).subscribe(response => {
      this.usersList = response.data;
    })
  }
  // Sorting Functions
  sortByOrder() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getAllocationOrders();
  }
  sortByDate() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getAllocationOrders();
  }
  sortByDistributed() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getAllocationOrders();
  }
  sortByCreated() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getAllocationOrders();
  }
  sortByStatus() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getAllocationOrders();
  }

  /**
   * pagination
   * @param page page no
   */
  setPage(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getAllocationOrders();
  }

}
